.main {
    display: flex;
    flex-direction: column;
    margin: 50px 0 0 0;
}

.content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    flex-direction: row;
}

.header {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.title {
    color: white;
    width: 100%;
    text-align: center;
}

.list {
    font-size: 16px;
    color: white;
    margin: auto;
    width: min-content;
}

.list ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    color: white;
}

.list ul li {
    margin: 0 25px 0 0;
    text-decoration: none;
    display: inline-block;
    font-weight: 500;
    transition: all .3s ease;
    cursor: pointer;
}

ul li:hover {
    transform: scale(1.2);
}

.unActive {
    color: #767676;
}

.active {
    transform: scale(1.2);
    color: white;
}

@media (min-width: 280px) and (max-width: 1099px) {
    .content {
        width: 300px;
    }
}